import { Router } from '@angular/router';
import { Component, OnInit } from '@angular/core';
import { CommonService } from '../common.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  email:string='';
  password:string='';

  invalid:boolean = false;
  noAccount:boolean = false;
  
  userDetails:any[]=[];

  flag:boolean = false;

  constructor(private _cserv:CommonService,
              private _router:Router) { }


  ngOnInit() {
  }

  login() {

    this._cserv.userdetails().subscribe((result)=>{

    if (result) {

      const obj= result;
      this.userDetails = Object.entries(obj).map(([type, value]) => ({type, value}));

      console.log ('UserDetails:',this.userDetails)

      this.userDetails.forEach(element =>
        {
          if (this.email === element.value.email && this.password === element.value.password) {
            this._router.navigate(['/','listRestaurents'])
            this.flag = true;
            console.log('success','Success')
        }
        
      }); 

      if (!this.flag) {
        this.invalid = true;
      }

    }

    })

  }

}
