import { CommonService } from './../common.service';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-list-restaurents',
  templateUrl: './list-restaurents.component.html',
  styleUrls: ['./list-restaurents.component.css']
})
export class ListRestaurentsComponent implements OnInit {

  public restoList:any;
  constructor(private _commonServ:CommonService) { }

  ngOnInit() {

    this._commonServ.getRestoList().subscribe(result=>{
      this.restoList = result;
    })

  }

}
