import { RegisterComponent } from './register/register.component';
import { LoginComponent } from './login/login.component';
import { ListRestaurentsComponent } from './list-restaurents/list-restaurents.component';
import { UpdateRestaurentComponent } from './update-restaurent/update-restaurent.component';
import { AddRestaurentComponent } from './add-restaurent/add-restaurent.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { FormsModule } from '@angular/forms';
import { CommonModule } from '@angular/common';
import { ReactiveFormsModule } from '@angular/forms';
import { HomeComponent } from './home/home.component';


const routes: Routes = [
  {path:'', component:HomeComponent},
  {path:'addRestaurent', component:AddRestaurentComponent},
  {path:'updateRestaurent/:id', component:UpdateRestaurentComponent},
  {path:'listRestaurents', component:ListRestaurentsComponent},
  {path:'login', component:LoginComponent},
  {path:'register', component:RegisterComponent}
];

@NgModule({
  declarations: [
    AddRestaurentComponent,
    UpdateRestaurentComponent,
    ListRestaurentsComponent,
    LoginComponent,
    RegisterComponent,
    HomeComponent

  ],
  imports: [RouterModule.forRoot(routes),
            FormsModule,
            CommonModule,
            ReactiveFormsModule],
  exports: [RouterModule]
})
export class AppRoutingModule { }
