import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { CommonService } from '../common.service';


@Component({
  selector: 'app-add-restaurent',
  templateUrl: './add-restaurent.component.html',
  styleUrls: ['./add-restaurent.component.css']
})
export class AddRestaurentComponent implements OnInit {

  alertMsg:boolean = false;
  addResto = new FormGroup(
    {
      title:new FormControl(''),
      address:new FormControl(''),
      mobail:new FormControl(''),
      email:new FormControl('')
    }
  )
  constructor(private _commonServ:CommonService) { }

  ngOnInit() {
  }

  add(){
    this._commonServ.addResto(this.addResto.value).subscribe(result=>{
      this.alertMsg = true
      this.addResto.reset({})
      console.log('result',result);
    });
  }

  closeAlert(){

     this.alertMsg = false;

  }
   

}
