import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router'
import { CommonService } from '../common.service';

@Component({
  selector: 'app-update-restaurent',
  templateUrl: './update-restaurent.component.html',
  styleUrls: ['./update-restaurent.component.css']
})
export class UpdateRestaurentComponent implements OnInit {

  alertMsg:boolean = false;
  editResto = new FormGroup (
    {
      title:new FormControl(''),
      address:new FormControl(''),
      mobail:new FormControl(''),
      email:new FormControl('')
    }
  )

  constructor(private _route:ActivatedRoute,
              private _cserv:CommonService,
              private _router:Router) { }

  ngOnInit() {

    console.log('id:', this._route.snapshot.params.id)
    this._cserv.getResto(this._route.snapshot.params.id).subscribe((result)=>{

      console.log('result:',result)
      this.editResto = new FormGroup(
        {
           title:new FormControl(result['title']),
            address:new FormControl(result['address']),
           mobail:new FormControl(result['mobail']),
           email:new FormControl(result['email'])
        }
      )
    })

  }

  update(){

    this._cserv.updateResto(this._route.snapshot.params.id,this.editResto.value).subscribe(()=>{
      this.alertMsg = true;
      this.editResto.reset({})
      this._router.navigate(['/','listRestaurents'])

    })

  }

  closeAlert() {
    this.alertMsg = false;
  }

}
